.castList{
    display: grid;
    max-width: calc(1600px - 48px);
    grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
    grid-gap: 16px;
    margin-top: 0;
    margin-bottom: 0;
    padding: 0;
    list-style: none;
    margin-left: auto;
    margin-right: auto;
}
.castListItem {
    border-radius: 2px;
    box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2),
      0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  }
  .castListImg{
    display: block;
    max-width: 100%;
    height: auto;
  }
  .castName{
    margin-left: 20px;
    font-size: 18px;
    color: #212121;
  }